import request from '../utils/request'

//è¯·æ±‚é€»è¾‘å®ä¾‹åŒ–

const Book = {

    getList: (params) => {
        let data = request.get('/book/list', params)
            .then((response) => {
                return response
            })
        return data
    },
    openBook: (params = {}) => {
        let data = request.get(`/_api2/read/openBook`, params)
            .then((response) => {
                return response
            })
        return data
    }

}

export default Book